import styled from 'styled-components';

export const MapBox = styled.div`
    width: 100%;
    height: 100%;
    position: relative;
`;
export const MapTab = styled.div`
    position: absolute;
    top: 10px;
    left: 20px;
    z-index: 998;
    width: 150px;
    height: 30px;
    background: white;
    border-radius: 6px;
    cursor: pointer;
    display: flex;
`;
export const MapTabItem = styled.div`
    width: 50%;
    background: ${(props) =>
        props.isActive ? 'linear-gradient(180deg, #808080 0%, #DCDCDC 100%)' : 'white'};
    box-shadow: ${(props) => (props.isActive ? '0px 0px 10px rgba(169, 169, 169, 0.52)' : '')};
    border-radius: 3px;
    flex: 1;
    color: ${(props) => (props.isActive ? 'white' : '#333')};
    text-align: center;
    line-height: 28px;
`;
