import React, { useCallback, useMemo, useState } from 'react';

import LeafletMap from '../LeafletMap';
import BaiduMap from '../BaiduMap';

import './index.scss';

const Home = (props) => {
    const [mapType, setMapType] = useState('leaflet');

    // 修改地图类型
    const changeMap = useCallback((type) => {
        setMapType(type);
    }, []);

    return (
        <div id="homePage">
            {mapType === 'leaflet' && <LeafletMap changeMap={changeMap} />}
            {mapType === 'baidu' && <BaiduMap changeMap={changeMap} />}
        </div>
    );
};

export default Home;
