import React, { useEffect } from 'react';

import { chinaUrl } from './const';

import { MapBox, MapTab, MapTabItem } from './styled';

const { L } = window;
let map;

const LeafletMap = React.memo((props) => {
    const { changeMap } = props;

    useEffect(() => {
        loadChinaMap();
    }, []);

    // 加载中国地图
    const loadChinaMap = () => {
        map = L.map('map', {
            center: { lon: 113.30307, lat: 23.28787 },
            maxZoom: 18,
            minZoom: 1,
            zoom: 11,
            zoomControl: false,
            preferCanvas: true, //使用canvas模式渲染path类矢量图层，性能优于svg
        });
        L.supermap.tiledMapLayer(chinaUrl, { transparent: false }).addTo(map);
    };

    return (
        <MapBox>
            <div id="map" style={{ width: '100%', height: '100%' }} />
            <MapTab>
                <MapTabItem isActive={true}>SuperMap</MapTabItem>
                <MapTabItem onClick={() => changeMap('baidu')}>百度</MapTabItem>
            </MapTab>
        </MapBox>
    );
});

export default LeafletMap;
