import React, { useRef, useEffect } from 'react';
import BMapGL from 'BMapGL';

import { MapBox } from './styled';
import { MapTab, MapTabItem } from '../LeafletMap/styled';

const BaiduMap = React.memo((props) => {
    const { changeMap } = props;
    const map = useRef();
    const mapDom = useRef();

    useEffect(() => {
        map.current = new BMapGL.Map(mapDom.current);
        map.current.centerAndZoom(new BMapGL.Point(113.30307, 23.28787), 12);
        map.current.enableScrollWheelZoom(true); // 开启鼠标滚轮缩放
        const scaleCtrl = new BMapGL.ScaleControl({ offset: new BMapGL.Size(5, 22) }); // 添加比例尺控件
        map.current.addControl(scaleCtrl);
    }, []);

    return (
        <MapBox>
            <div className="mapBox" ref={mapDom}></div>
            <MapTab>
                <MapTabItem onClick={() => changeMap('leaflet')}>SuperMap</MapTabItem>
                <MapTabItem isActive={true}>百度</MapTabItem>
            </MapTab>
        </MapBox>
    );
});

export default BaiduMap;
